.App {
  padding: 15vh 1em 0;
  width: 100%;
}

.App section {
  width: 100%;
}

.Navbar {
  background-color: #090909;
  justify-content: space-between;
  color: #fff;
  padding: 0 0.5em;
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 15vh;
  z-index: 1;
}

.Navbar h1 {
  font-size: 1em;
  font-weight: 500;
  letter-spacing: 1px;
}

.Navbar h1 span {
  color: red;
  font-weight: 700;
}

.Navbar .logo {
  width: 3.3em;
  margin-right: 0.5em;
}

.Navbar ul {
  list-style-type: none;
  justify-content: space-around;
  width: 33%;
}

.navlinks { display: none; }

.navlink-wrapper {
  position: relative;
  width: 3.5em;
  height: 1.5em;
}

.navlink {
  color: #999;
  font-size: 0.95em;
  font-weight: 500;
  letter-spacing: 1px;
  transition: 0.3s ease-out;
}

.navlink:hover { color: #fff; }

.navlink-wrapper svg {
  pointer-events: none;
  position: absolute;
  left: 0;
  top: -30%;
  transform: rotateY(180deg);
  transition: 0.3s ease;
}

.heartbeat path {
  fill-opacity: 0;
  opacity: 0;
  stroke: var(--main-color);
  stroke-width: 1em;
  stroke-dasharray: 1400;
  transition: 0.3s ease-out;
}

@keyframes pulse {
  0% { stroke-dashoffset: 1400; }
  100% { stroke-dashoffset: 0; }
}

.heartbeat.animate path {
  animation: pulse 0.3s ease-out;
  opacity: 1;
}

.menu-toggle {
  position: relative;
  width: 40px;
  height: 32px;
}

.menu-toggle .overlay {
  background-color: rgba(0,0,0,0.5);
  opacity: 0;
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100vh;
  transform: translateX(-100%);
  transition: 0.3s ease-out;
}

.menu-toggle .overlay.active {
  opacity: 1;
  transform: translateX(0);
}

.menu-toggle button {
  position: absolute;
  width: 40px;
  height: 32px;
  z-index: 2;
}

.menu-toggle .burger-slice {
  background-color: #fff;
  border-radius: 3px;
  height: 4px;
  width: 33px;
  margin-bottom: 5px;
  position: relative;
  transform-origin: 4px 0;
  transition: transform 0.5s cubic-bezier(0.77,0.2,0.05,1.0),
              background 0.5s cubic-bezier(0.77,0.2,0.05,1.0),
              opacity 0.55s ease;
}

.menu-toggle .burger-slice:first-child { transform-origin: 0 0; }
.menu-toggle .burger-slice:last-child { transform-origin: 0 100%; }

.menu-toggle button.active .burger-slice {
  background-color: #090909;
  opacity: 1;
  transform: rotate(45deg) translate(-2px, -1px);
}

.menu-toggle button.active .burger-slice:nth-child(2) {
  opacity: 0;
  transform: rotate(0) scale(0.2, 0.2);
}

.menu-toggle button.active .burger-slice:last-child {
  transform: rotate(-45deg) translate(-2px, 1px);
}

.menu-wrapper.container {
  background-color: #fff;
  flex-direction: column;
  height: 100vh;
  width: 80%;
  position: fixed;
  right: 0;
  top: 0;
  transform-origin: 0 0;
  transform: translate(100%, 0);
  transition: transform 0.5s cubic-bezier(0.77,0.2,0.05,1.0);
  z-index: 1;
}

.menu-wrapper ul {
  flex-direction: column;
  justify-content: space-evenly;
  height: 40%;
  width: 100%;
}

.menu-wrapper .navlink {
  color: #090909;
  font-size: 1.5em;
  margin: 2em;
}

.menu-wrapper.active {
  transform: translate(0, 0);
}

#home {
  color: #090909;
  flex-direction: column;
  height: 85vh;
  margin-bottom: 3em;
}

#home img {
  width: 100%;
  max-width: 500px;
}

.call-to-action { text-align: center; }

.call-to-action h2 {
  font-size: 1.8em;
  font-weight: 500;
  letter-spacing: 1px;
  margin-bottom: 1em;
}

.call-to-action button {
  background-color: var(--main-color);
  border-radius: 100vmax;
  box-shadow: 0 0 1px rgba(0,0,0,0.3), 0 3px 6px 1px rgba(0,0,0,0.3);
  color: #fff;
  font-size: 0.9em;
  font-weight: 600;
  letter-spacing: 1.5px;
  padding: 0.5em 1em;
  width: 9em;
  height: 3em;
}

#about {
  padding-top: 15vh;
}

#about h2,
#contact h2,
.services h2 {
  font-weight: 500;
  margin-bottom: 2em;
  position: relative;
  text-align: center;
  z-index: -1;
}

#about h2::after,
#contact h2::after,
.services h2::after {
  background-color: var(--main-color);
  border-radius: 2px;
  content: '';
  position: absolute;
  left: 50%;
  bottom: -50%;
  transform: translateX(-50%);
  width: 96px;
  height: 2px;
}

.more-info {
  flex-direction: column;
  margin: auto;
}

.more-info img {
  width: 100%;
  max-width: 500px;
}

.more-info p,
#contact p {
  font-size: 1.1em;
  line-height: 1.4em;
  margin: 2em 0;
  text-align: center;
}

.services {
  flex-direction: column;
  margin: 2em auto;
  position: relative;
  width: 80%;
}

.services ul li {
  font-size: 1.2em;
  padding: 0.5em 0;
}

#contact {
  flex-direction: column;
  padding: 15vh 0;
}

#contact p { margin: 0 0 2em; }

#contact ul {
  flex-direction: column;
  list-style-type: none;
  width: 80%;
}

#contact ul li {
  margin-bottom: 1em;
  width: 100%;
}

.contact-icon {
  display: inline-block;
  font-size: 1.5em;
  margin-right: 1em;
}

@media screen and (min-width: 375px) {
  .Navbar { padding: 0 1em; }

  .Navbar .logo { width: 4em; }

  .Navbar h1 { font-size: 1.2em; }
}

@media screen and (min-width: 768px) {
  .Navbar { padding: 0 2em; }

  .Navbar .logo { width: 4.5em; }

  .Navbar h1 { font-size: 1.5em; }

  .menu-toggle {display: none; }

  .navlinks { display: flex; }

  #home { flex-direction: row; }

  .more-info { flex-direction: row; }

  .more-info img { margin-right: 2em; }
}

@media screen and (min-width: 992px) {
  .more-info { width: 80%; }
}
